import BasicTitle from "@/Components/BasicTitle/BasicTitle";
import Image from "next/image";
import DefaultImage from '@/public/images/Default-Image.png'

export default function GamersPage() {
    let NumberPlayer = 1;
    const data = [

        {
            status: 'Вратари', info: new Array(4).fill(null).map((value, index) => {
                const infoPlayer = { name: `Имя`, surname: "Фамилия", number: NumberPlayer };
                NumberPlayer * 2;
                return (infoPlayer)
            }),
        },
        {
            status: 'Защитники', info: new Array(6).fill(null).map((value, index) => {
                const infoPlayer = { name: `Имя`, surname: "Фамилия", number: NumberPlayer };
                NumberPlayer += 1;
                return (infoPlayer)
            }),
        },
        {
            status: 'Полузащитники', info: new Array(8).fill(null).map((value, index) => {
                const infoPlayer = { name: `Имя`, surname: "Фамилия", number: NumberPlayer };
                NumberPlayer += 1;
                return (infoPlayer)
            }),
        },
        {
            status: 'Нападающие', info: new Array(3).fill(null).map((value, index) => {
                const infoPlayer = { name: `Имя`, surname: "Фамилия", number: NumberPlayer };
                NumberPlayer += 1;
                return (infoPlayer)
            }),
        }
    ]
    return (
        <div className="w-full flex flex-col justify-center items-center">
            <div className="max-w-[1200px] text-center">
                <BasicTitle text="Состав команды" />
                <div className="w-full">
                    <Image className="w-full rounded-[10px]" alt="Состав команды Зенит Ижевск" src={DefaultImage} />
                </div>
            </div>
        </div>
    )
}